import React, { useState } from "react";
import MatchEntity, {
  Participant,
} from "../../../../../../../../../model/match-entity";

interface Props {
  user: Participant | undefined;
  match: MatchEntity | undefined;
}

function ScoreBoard(props: Props) {
  const [scoreRank, setScoreRank] = useState(false);
  // const teamInfo = props.match?.teams
  const teamInfo = props.match?.teams?.find(
    (team) => team.teamId === props.user?.teamId
  );

  return (
    <div
      className="flex justify-center cursor-pointer select-none w-72"
      onClick={() => {
        setScoreRank(!scoreRank);
      }}
    >
      <table className="text-gray-600">
        <thead>
          <tr className="text-xs">
            <th className="w-16">킬</th>
            <th className="w-16 text-red-600">데스</th>
            <th className="w-16">어시스트</th>
            <th className="w-16">
              {props.user?.individualPosition === "UTILITY" ? "와드" : "CS"}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr className="text-xl font-extrabold">
            <td>
              <div className="flex gap-1 justify-center">
                {props.user?.kills || 0}
                {scoreRank && Rank()}
              </div>
            </td>
            <td className="text-red-600">
              <div className="flex gap-1 justify-center">
                {props.user?.deaths || 0}
                {scoreRank && Rank()}
              </div>
            </td>
            <td>
              <div className="flex gap-1 justify-center">
                {props.user?.assists || 0}
                {scoreRank && Rank()}
              </div>
            </td>
            <td>
              <div className="flex gap-1 justify-center">
                {props.user?.individualPosition === "UTILITY"
                  ? props.user.wardsPlaced
                  : (props.user?.totalMinionsKilled as number) +
                      (props.user?.neutralMinionsKilled as number) || 0}
                {scoreRank && Rank()}
              </div>
            </td>
          </tr>
          <tr className="text-xs">
            <td colSpan={3}>
              <div className="flex gap-3 justify-center">
                <div>{`평점 ${(
                  ((props.user?.kills as number) +
                    (props.user?.assists as number)) /
                  (props.user?.deaths as number)
                ).toFixed(2)}`}</div>
                <div>
                  {`킬관여
                  ${Math.round(
                    (((props.user?.kills as number) +
                      (props.user?.assists as number)) /
                      (teamInfo?.objectives?.champion?.kills as number)) *
                      100
                  )}%`}
                </div>
              </div>
            </td>
            <td>
              {(
                ((props.user?.totalMinionsKilled as number) +
                  (props.user?.neutralMinionsKilled as number)) /
                new Date(
                  (props.match?.gameDuration as number) * 1000
                ).getMinutes()
              ).toFixed(2)}
              /분
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

function Rank() {
  return <div className="text-xs font-semibold self-end mb-[4px]">등</div>;
}

export default ScoreBoard;
